import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import ProjectCard from "./ProjectCards";
import Particle from "../Particle";

import planitium from "../../Assets/Projects/planitium.png";
import mastermindad from "../../Assets/Projects/mastermindad.png";
import alphaglass from "../../Assets/Projects/alphaglass.png";
import interglass from "../../Assets/Projects/interglass.png";
import casal from "../../Assets/Projects/casal.png";
import campus7 from "../../Assets/Projects/campus7.png";


function Projects() {
  return (
    <Container fluid className="project-section">
      <Particle />
      <Container>

        <Row style={{ justifyContent: "center", paddingBottom: "10px" }}>
          <Col md={12}>
            <h1 className="project-heading">
              My Recent <strong className="purple">Works </strong>
            </h1>
            {/* <p style={{ color: "white" }}>
              Here are a few projects I've worked on recently.
            </p> */}
          </Col>
          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={alphaglass}
              isBlog={false}
              title="Alphaglass"
              description="Website Designed and Integrated with Wordpress for Dubai Aluminium and Glass Manufacturers"
              link="https://alphaglass.org/"
            />
          </Col>
          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={interglass}
              isBlog={false}
              title="Interglass"
              description="Website Designed and Integrated with Wordpress"
              link="https://interglass.org/"
            />
          </Col>
          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={casal}
              isBlog={false}
              title="Casal"
              description="Website Designed and Integrated with Wordpress"
              link="https://casal.in/"
            />
          </Col>
          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={campus7}
              isBlog={false}
              title="Campus7"
              description="Landing Page Design + Html Convertion"
              link="https://campus7.in/cloud-erp-for-schools-and-colleges/"
            />
          </Col>
          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={planitium}
              isBlog={false}
              title="Planitium"
              description="Website Integrated with Wordpress for Dubai Based Client"
              link="https://planitium.com"
            />
          </Col>
          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={mastermindad}
              isBlog={false}
              title="Mastermindad"
              description="Website Integrated with Wordpress for Dubai Based Advertizing agency"
              link="https://mastermindad.com/"
            />
          </Col>

        </Row>
      </Container>
    </Container>
  );
}

export default Projects;
