import React from "react";
import Card from "react-bootstrap/Card";
import { ImPointRight } from "react-icons/im";

function AboutCard() {
  return (
    <Card className="quote-card-view">
      <Card.Body>
        <blockquote className="blockquote mb-0">
          <p style={{ textAlign: "left" }}>
            Hi, I am <span className="purple">Vysakh PS </span>
            from <span className="purple"> Kerala, India.</span>
            <br />Currently working on Web and Graphics Designing + Development for 2.5 Years
            <br />
            <br />
            Other free time activities that i loves!
          </p>
          <ul>
            <li className="about-activity">
              <ImPointRight /> Playing Games
            </li>
            <li className="about-activity">
              <ImPointRight /> Surfing Internet
            </li>
            {/* <li className="about-activity">
              <ImPointRight /> Travelling
            </li> */}
          </ul>

          <p style={{ marginBlockEnd: 0, color: "rgb(155 126 172)" }}>
            "Strive to build things that make a difference!"{" "}
          </p>
          <footer className="blockquote-footer">Vysakh</footer>
        </blockquote>
      </Card.Body>
    </Card>
  );
}

export default AboutCard;
